@import "common/mixins";
@import "common/variables";

.quotes {
  &__body {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0px auto;
    padding: 20px 0px 20px 0px;
  }
}

// -----   -----------------
.plaza-quotes {
  &__body {
    @include fdc();
    min-width: 100%;
    height: 100%;
  }
}
// ----------  ---------------------------
.item-plaza-quotes {
  position: relative;
  &:after,
  &:before {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border: 1px solid #b29c69;
    left: 40%;
    z-index: 2;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  &:after {
    left: 40%;
  }
  &:before {
    left: 60%;
  }

  &__prev {
    position: absolute;
    background-color: #b29c69;
    width: 11.26315%;
    height: 1px;
    top: 50%;
    left: 32%;
    transform: translate(-50%, -50%);

    &_next {
      left: 68%;
    }
  }

  &__link {
    @include center();
    padding: 10px 0px;
    animation: rotate 5s infinite linear;
  }
}
// ----------   ---------------------------
.item-plaza-quotes-duble {
  &__body {
    width: 100%;
    height: 100%;
    @include flex();
    @media screen and (max-width: 740px) {
      @include fdc();
      @include alic();
    }
  }
  &__left {
    flex: 0 1 66.32%;
  }
  &__right {
    flex: 1 1 33.68%;
    @include center();
  }
}

.left-item-plaza-quotes-duble__text {
  padding: 40px 0px 0px 0px;
@media screen and (max-width: 700px) {
  padding: 40px 10px 0px 10px;
}
  @include fdc();
  p {
    @include Text-Style-16;
    text-align: start;
    font-style: italic;
    @media screen and (max-width: 740px) {
      text-align: center;
    }
  }
  span {
    font-family: PTSans;
    font-size: 15px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
    text-align: left;
    @media screen and (max-width: 740px) {
      text-align: center;
    }
    color: $dark;
  }
}
