@import "common/mixins";
@import "common/variables";



// --- 
.btn {
  position: relative;
   width: 27vw; 
  display: inline-flex;
  z-index: 5;
  &_big{

  span{
    span{ 
      @include Text-Style-7;
    }
   }
}
  
  &:after,
  &:before {
    position: absolute;
    content: "";
    left: 0;
    height: 3px;
    width: 100%;

  }

  &:before {
    background-color: $very-light-brown-two;
    left: 0;
    top: 0;
    
  }

  &:after {
    background-color: $dirt-brown-three;
    left: 0;
    bottom: 0;
  }

  &__body {
    @include alic();
    @include jucc();
    min-height: 50px;

    &:after,
    &:before {
      content: "";
      position: absolute;
      height: 100%;
      top: 0;
      width: 3px;
      background-image: linear-gradient(to top, $dirt-brown-three, $very-light-brown-two);

    }

    &:before {
      left: 0;
    }

    &:after {
      right: 0;
    }

    span {
      display: block;
      padding: 0px 50px;
      @include Text-Style-5;
      text-transform: uppercase;
       
      &:after {
        @include anim(0.2s);
        content: "";
        width: 100%;
        height: 50%;
        top: 0;
        left: 0;
        position: absolute;
        opacity: 0.1;
        background-image: linear-gradient(to top, $sandy-brown, $sandy-brown);
      }
    }
  }
   
  @media screen and (max-width: 1992px) {
    &:hover {
      .btn__body span {
        &:after {
          height: 100%;
        }
      }
    }
  }
  
  @media screen and (max-width: 400px) {
     width: 150px;
    span {
        font-size: 12px;
      }
  }
@media screen and (max-width: 900px) {
      width: 200px;
      span {
        font-size: 14px;
      }
  }
} 

// ------ ----
.title {
text-align: center;
  &__item {
    @include Text-Style-2;
    margin: 0px 0px 20px/40px*1em 0px;
  }

  &__main {
    @include Text-Style-3;
    text-transform: uppercase;
    font-weight: 400;
    
  }

}
// --------------------------
.block-header {
&__title {  
margin: 0px 0px 20px 0px;
}
		// .block-header__decor

		&__decor {
		}
}

.block-decor {
@include flex();

		&__items {
     flex: 1 1 auto;
      @include alic();
      &:before {
            content: "";
            height: 1px;
         flex: 1 1 100%;
            background-color: #795D2E;
@media screen and (max-width: 676px) {
    flex: 0 1 50%;
    margin: 0px 20px 0px 20px;
   
}
      }
     
  
      &:after {
            content: "";
        width: 25px*0.7;
        height: 25px*0.7; 
        border: 1px solid #795D2E ;
        @include rotate(-45deg);
        flex: 0 0 25px*0.7;
           margin: 0px 5px 0px 5px ;
      }
      span{
        margin: 0px 5px ;
        @include alic();
         &:before {
            content: "";
         width: 10px;
            flex: 0 0 10px; 
            height: 10px;
         border-radius: 50%; 
          border: 1px solid #795D2E ;
          margin: 0px 5px ; 
      }
      
      &:after {
            content: "";
        flex: 0 0 15px*0.7;
        width: 10px;
        height: 15px*0.7; 
        border: 1px solid #795D2E ;
        @include rotate(-45deg);
         
      }
       }
       &:last-child{ 
         flex-direction: row-reverse;
         span{ flex-direction: row-reverse;   }
       }
		}
		&__icon {
      flex: 0 1 145px;
      text-align: center;
       padding: 25px 0px 0px 0px; 
       img{max-width: 70px; }
		}
}










