.lots {
  padding: 92px 0px 0px 0px;
   margin: -75px 0px 0px 0px;
  border-top: 75px solid transparent;
}

.slider-lots {
  &__body {
    @include jucc();
    flex-wrap: wrap;
    @media screen and (max-width: 850px) {
      @include jucc();
    }
  }
}

.title__main_dark {
  color: #000;
}

.lots-card {
  
  background-color: white;
  max-width: 250px;
  position: relative;
  @media screen and (max-width: 850px) {
    margin: 40px 20px;
  }

  &__body {
    @include fdc();
    @include alic();
    position: relative;
    min-height: 100%;
    background-color: white;
  }
  &__img {
    display: block;
    width: 100%;
height: 100%;

    position: relative;
  }
  &__text {
    @include Text-Style-7;
    z-index: 10;
    min-height: 70px;
    max-width: 95%;
  }
  &__btn {
    max-width: 89%;
    margin: 30px 0px 0px 0px;
    span {
      span {
        padding: 0px 0px 0px 0px;
      }
    }
  }

  &__small {
    flex: 0 1 20%;
    background-color: white;
    padding: 40px 0px 0px 0px;

    &_big {
      flex: 0 1 60%;
    }
  }
  &__item {
    position: absolute;
    width: 100%;
    // height:100%;
    left: 0;
    top: -27px;
  }
}
.blocks {
  position: relative;
  @include jucb();
  width: 100%;height: 100%;
}

.item-lots-card {
  position: relative;
  z-index: 1;
  &:after {
      content: "";
      background-color: #ffffff;
      position: absolute;
      width: 70px;
      height: 70px;
      top: 100%;
      left: 50%;
      z-index: 2;
      transform: translate3d(-50%, -50%, 0px) rotate(45deg);
    }

  &__text {
    position: relative;
    z-index: 100;
    padding: 8px 0px 0px 0px;  
    @include Text-Style-9;
    text-align: center;
    letter-spacing: -0.5px;
    color: #000;
  }
}

.lots-card-img {
  padding: 0px 0px 100% 0px;
  width: 100%;height: 100%;
  &__body {
    @include jucc();
    width: 100%;
  }
}

.icon-view-show,
.icon-hand-stop {
  position: relative;
  color: white;
  &:before {
    z-index: 2;
    top: 0px;
  }
}

.item-lot {
  &__text {
    padding: 20px 0px 0px 0px;
    position: relative;
    &:before {
      content: "";
      position: absolute;
      width: 70px;
      height: 70px;
      border: 2px solid #d4b584;
      top: -240%;
      left: 50%;
      transform: translate3d(-50%, 0px, 0px) rotate(45deg);
      z-index: 1;
    }

    &_high {
      @include Text-Style-7;
    }
    &_bet {
      @include Text-Style-9;
      line-height: 0px;
      position: relative;
      z-index: 4;
    }
    &_big {
      padding: 30px 0px 0px 0px;
      @include Text-Style-8;
      z-index: 4;
      position: relative;
    }
  }
}

.item-blockkk-small {
  position: relative;
  &_tiny {
    position: relative;
    &:after {
      content: "";
      background-color: #DAC096;
      position: absolute;
      width: 80%;
      height: 1px;
      top: 57%;
      z-index: 2;
      left: center;
    }
  }

  &__icon {
    &:after {
      content: "";
      background-color: #000000;
      position: absolute;
      width: 20px;
      height: 20px;
      top: -50%;
      left: 50%;
      transform: translate3d(-50%, 50%, 0px) rotate(45deg);
    }
  }

  &__text {
    padding: 15px 0px 0px 0px;
    @include Text-Style-9;
  }
}



.control-slider-lots {
  @include center();
  padding: 50px 0px 0px 0px;

  &__link {
    flex: 0 0 100px;
    height: 100px;
    background: url(../img/decor/decor-5.jpg) no-repeat;
    @include center();
    @include Text-Style-7;
    margin: 0px 20px;
  }

  &__arrow {
    background-color: #e628ff;
    cursor: pointer;
    height: 35px;
    flex: 0 0 108px;
    background: url(../img/decor/decor-2.jpg) no-repeat;
    position: relative;
    @media screen and (max-width: 400px) {
      flex: 0 0 40px;
    }
    &:after {
      content: "";
      position: absolute;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      border: 1px solid #a9a18e;
      top: 50%;
      right: -15px;
      z-index: 2;
      @include tr(0px, -50%, 0px);
    }

    &:last-child {
      @include rotate(-180deg);
    }
  }
}
