@import "common/mixins";
@import "common/variables";


.main-slider {
   position: relative;
 margin: -75px 0px 0px 0px;
  border-top: 150px solid transparent;
   &__body {
      @include fdc();
   }

}

 

@keyframes rotate {
   0% {}

   100% {
      transform: rotate(360deg);
   }
}
.item-main-slider {
      position: relative;
      padding: 30px 0px 100px 0px;
      text-align: center;
      min-height: 450px;

      &__content {
         position: relative;
         z-index: 2;
          min-height: 250px;
          @media screen and (max-width: 767px) {
            min-height: 300px;
          }
                    @media screen and (max-width: 480px) {
            min-height: 400px;
          }
      } 

      &__bg {
         position: absolute;
         width: 100%;
         height: 100%;
         top: 0;
         left: 0;
      }

      &__text {
         @include Text-Style-5;
         max-width: 700px;
         margin: 25px auto 0px auto;
         overflow: hidden;
         line-height: calc(25/15);
         @media screen and (max-width: 767px) {
            font-size: 12px;
          }
      }
@media screen and (max-width: 700px) {
  padding: 30px 20px 100px 20px;
}
   }
// ====================================================
.control-main-slider {
   position: absolute;
      bottom: 0px;
      left: 50%;
      width: 80px;
      height: 80px;
      @include tr(-50%, 50%, 0px);
      @include jucb();
      @include alic();
      &:after {
         content: "";
         position: absolute;
         width: 100%;
         height: 100%;
         top: 0;
         left: 0;
         background-color: #fff;
         transform: rotate(-45deg);

      }

&__decor {
      position: relative;
      z-index: 2;
      width: 30px;
      height: 30px;
      background: url(../img/img-14.jpg) 50% 0% no-repeat;
      animation: rotate 5s infinite linear;
      img {
         width: 100%;
         height: 100%;
      }
   }

   &__arrow {
      position: relative;
      z-index: 2;
      width: 15px; 
      height: 20px;
      font-size: 30px;
      color: #B49A6B;
      cursor: pointer;
  &:last-child{
     @include rotate(180deg);
  }
}
}  
