// -------------------------------------------------------------

.icomoon {
  width: 100%;
  height: 100%;
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  &:before {
    position: absolute;
    @include tr(-50%, -50%, 0px);
    top: 50%;
    left: 50%;
    z-index: 100;
  }
}

