@import "common/mixins";
@import "common/variables";
// ---------------------------------------
.categories {
  margin: -75px 0px 0px 0px;
  border-top: 175px solid transparent;
  @media screen and (max-width: 960px) {
    padding: 0px 20px;
  }
  &__body {
   @include jucb();
flex-wrap: wrap;
padding: 0px 0px 75px 0px;
 @media screen and (max-width: 960px) {
   @include jucc();
 }
  }
  &__item {
     flex: 0 1 20%;
    border-radius: 50%;
    @media screen and (max-width: 960px) {
    
      .item-categories__right{display: none; }  
      padding: 0px 0px 0px 0px;
  margin: 30px 30px 30px 30px;
    }
  }
}
.item-categories{ 
  position: relative;
  &__body {  
@include flex();
padding: 0px 100px 0px 0px;
@media screen and (max-width: 960px) {
  padding: 0px 0px 0px 0px;
}
}

&__left {  
background-color: #193549;
    @include fdc();
    @include alic();
height: 100%;
}

&__right {  
height: auto;
@include fdc();
position: absolute;
height:100%;
top: 0;
left: 250px;

}
}

.block-main {
  position: relative;
  z-index: 2;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  background-size: cover;
  @include fdc();
  @include alic();
  &__link {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 20;
    border-radius: 50%;
  }
  &__icon {
    position: relative;
    z-index: 4;
    padding: 23px 0px 0px 0px;   
  }
  &__text {
    @include Text-Style-14;
    text-transform: uppercase;
    padding: 33px 20px 0px 20px;
    z-index: 4;
    position: relative;
    max-height: 59px;
&:after {
            content: "";
           position: absolute;
           width: 10px;
            height: 10px;
            top: 296%;
            z-index: 2;
border-radius: 50%;
border: 1px solid #d4b584;
left: 50%;
@include tr(-50%,0px,0px);
@media screen and (max-width: 960px) {
  display: none;
}
      }
  }
  span {
    margin: 46px 0px 0px 0px;
    width: 20px;
    height: 20px;
    border: 1px solid #b29c69;
    position: relative;
    @include rotate(45deg);
    z-index: 4;

    &:after {
      content: "";
      background-color: #b29c69;
      position: absolute;
      width: 20px;
      height: 1px;
      left: 138%;
      z-index: 4;
      top: -98%;
      @include rotate(135deg);
    }
    &:before {
      content: "";
      background-color: #b29c69;
      position: absolute;
      width: 20px;
      height: 1px;
      right: 138%;
      z-index: 4;
      top: 191%;
      @include rotate(135deg);
    }
  
  }
  &__mask {
    background-color: #151b1f;
    opacity: 0.4;
    position: absolute;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 2;
    }
  }

.decor-11 {
  @include fdc();
  @include alic();
  @include jucc();
  position: relative;
  width: 100px;
  height: 250px;
  &__short {
    .decor-11__high {
      span {
        display: none;
      }
    }
  }

  &__high {
    top: -36px;
    left: 50%;
  }

  &__middle {
    span {
      @include flex();
      width: 10px;
      height: 10px;
      background-color: #b29c69;
      border-radius: 50%;
      position: relative;

      & span {
        position: absolute;
        width: 100px;
        background-color: #b29c69;
        height: 1px;
        top: 80px;
        left: 50%;
        border-radius: 0%;
        transform: translate3d(-50%, 0px, 0px) rotate(90deg);
        &:last-child {
          top: -71px;
        }
      }
    }
  }

  &__low {
    @include flex();
    padding: 100px 0px 0px 0px;
    bottom: -33px;
  }
}

.decor-11-adge{
    @include flex();
    position: absolute;
    left: 50%;
    span {
      transform: translate3d(-50%, 0px, 0px) rotate(45deg);
      width: 15px;
      height: 15px;
      border: 1px solid #b29c69;
    }
  }

.item-categories-short{ 
  .item-categories__right{display: none; }
  .item-categories__body{padding: 0px 0px 52px 0px; }
@media screen and (max-width: 960px) {
  .item-categories__body{padding: 0px 0px 0px 0px; }
}
}

.dotts-none{
  .block-main__text{
    &:after{
      display: none;
     }
   }
 }
.title-categories{
  
  &__body { 
     padding: 50px 0px 0px 0px;
@include flex();
}
&__item{
 flex: 0 1 20%;
border: 2px solid #d4b584;

 }
 }

.title-categories {
		&__body {
           padding: 50px 0px 0px 0px;
@include flex();
		}
		&__img {
       flex: 1 0 8.75%;
		}
		&__text {
             flex: 0 1 auto;
// border: 2px solid #d4b584;
padding: 10px 10px 20px 50px;
@include Text-Style-12;
		}
}

.img-title-categories {
  background: url(../img/decor-1.jpg) repeat-Y;
background-size: contain;
position: relative;

}
.text-title-categories {
 P{ margin: 0px 0px 25px 0px;
&:last-child{
