@import "common/mixins";
@import "common/variables";
@import "common/fonts";
.footer {
 background-color: #1C2329; 

  &__body {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0px auto;
    text-align: center;
    padding: 32px 0px 30px 0px;
  }
  
  &__button {
    margin: 20px 0px 0px 0px;
  }
}

// ------ --------------------
.header-footer {
  &__text {
    @include jucb();
    padding: 5px 0px 0px 0px;
    @media screen and (max-width: 820px) {
      padding: 5px 10px 0px 10px;
    }
    @media screen and (max-width: 500px) {
      @include fdc();
      @include alic();
    }
    span {
      font-family: PTSans;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      text-align: left;
      color: $sandy-brown;
    }
  }
  &__link {
    @media screen and (max-width: 500px) {
      padding: 30px 0px 0px 0px;
    }
    span {
      text-decoration: underline;
    }
    img {
      margin: 0px 5px 0px 0px;
    }
  }
}

// -----   -----------------
.plaza-footer {
  &__body {
    padding: 20px 0px 0px 0px;
    @include jucb();
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    @media screen and (max-width: 820px) {
      @include fdc();
      padding: 20px 10px 0px 10px;
    }
    nav {
      flex: 0 1 49%;
      @media screen and (max-width: 820px) {
       padding: 20px 0px 20px 0px;
      }
      ul {
        @include jucs();
        flex-wrap: wrap;
        li {
          padding: 0px 10px 0px 0px;
        }
      }
    }
  }

}
// ----------  ---------------------------
.item-plaza-footer {
  
  li {
    display: inline-flex;
  }
  &__item {
    span {
      font-family: PFDinTextCondPro;
      font-size: 14px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.79;
      letter-spacing: 1.4px;
      text-align: left;
      color: $white;
      text-transform: uppercase;
    }
  }

  &__right {
    span {
      font-family: BravoRG;
      font-size: 50px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: normal;
      text-align: left;
      color: $sandy-brown;
      position: relative;

      span {
        font-family: BravoRG;
        font-size: 25px;
        line-height: 1;
        text-align: left;
        color: $sandy-brown;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 7%;
        left: -15%;
      }
    }
  }
}
