@import "normalize";
.wrapper {
    overflow: hidden;
    @include fdc;
    min-height: 100vh;
}
// ---- ------
.page {
    
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.container{
    //    outline: 1px solid #8CBEFF;
    max-width: 950px;
       padding: 0px -15px;
       margin: 0 auto;
   }    
.ibg {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    img {
        display: none;
    }
}
a{
    @include center();
    
    &:hover{
        cursor: pointer;
    }

}
.lock {
    overflow: hidden;
}
.bg {
     position: absolute;
     width: 100%;
     height: 100%;
     top: 0;
     left: 0;
     z-index: 1;
}











