@import "common/mixins";
@import "common/variables";
@import "common/fonts";

.service {
  background-color: #1c2329;
  margin: -75px 0px 0px 0px;
  border-top: 75px solid transparent;
  position: relative;
  &:after {
    content: "";
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 75px;
    top: -77px;
    left: 0;
    z-index: 2;
  }
  &__body {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0px auto;
    text-align: center;
    padding: 63px 0px 20px 0px;
  }

}

// -----   -----------------
.plaza-service {
  &__body {
    padding: 20px 0px 0px 0px;
    @include jucb();
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    @media screen and (max-width: 676px) {
      @include jucc();
    }
  }
  &__item {
    @media screen and (max-width: 676px) {
      @include jucc();
      margin: 0px 20px 10px 20px;
    }
    flex: 0 1 26.315%;
    padding: 20px 0px 0px 0px;
  }
}
// ----------  ---------------------------
.item-plaza-service {
  @include fdc();
  @include alic();
  &__icon {
    width: 200px;
    height: 200px;
    padding: 20px 0px 0px 0px;
  }
  &__title {
    padding: 0px 5px;
    @include Text-Style-14;
    text-transform: uppercase;
  }
  &__text {
    padding: 20px 5px 0px 5px;
    @include Text-Style-15;
    line-height: 1.5;
    min-height: 200px;
    @media screen and (max-width: 460px) {
      min-height: 150px;
    }
  }
  &__button {
    margin: 30px auto;
  }
}
// ---------------------------------------------
.button-item-plaza-service,
.button-left-item-plaza-service-duble {
  width: 66.6%;

  @include jucc();
  @include anim(0.2s);
  padding: 5px 0px 5px 0px;
  position: relative;
  &:hover {
    span {
      color: #246eb9;
    }
  }
  span {
    padding: 5px 10px;
    width: 100%;
    height: 100%;
    @include Text-Style-14;
    text-transform: uppercase;
    position: relative;
    z-index: 4;
    background-color: #1c2329;
    @include anim(0.2s);
  }

  &:after {
    content: "";
    background-color: #b29c69;
    position: absolute;
    width: 147.6%;
    height: 1px;
    top: 50%;
    left: -100;
    z-index: 1;
  }
}
