.news {
  margin: -75px 0px 0px 0px;
  border-top: 75px solid transparent;
  &__body {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0px auto;
    padding: 20px 0px 70px 0px;
    @media screen and (max-width: 980px) {
      padding: 0px 10px 70px 10px;
    }
  }
}

// -----   -----------------
.plaza-news {
  @include flex();
  @media screen and (max-width: 600px) {
    @include fdc();
    padding: 0px 10px 0px 10px;
  }

  &__body {
    padding: 40px 0px 0px 0px;
    @include fdc();
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    flex: 0 1 50%;

    &_ivents {
      padding: 40px 0px 0px 20px;
      @media screen and (max-width: 600px) {
        padding: 40px 0px 0px 0px;
      }
    }
  }
  &__item {
    flex: 0 1 20%;
    &_duble {
    }
  }
}
// ------ --------------------
.header-news {
  span {
    @include flex();
    @include Text-Style-18;
    padding: 30px 0px 0px 0px;
  }

  &__title {
    font-family: PTSans;
    font-size: 15px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    text-align: left;
    color: $black-two;
    @include jucs();
    text-decoration: underline;
  }

  &__text {
    @include Text-Style-12;
    line-height: 1.2;
    &_solo {
      padding: 40px 0px 0px 0px;
    }
  }
}

// ----------   ---------------------------
.item-plaza-news-duble {
  &__body {
    width: 100%;
    height: 100%;
    @include flex();
  }
  &__left {
    flex: 0 1 auto;
  }
  &__right {
    flex: 0 1 8%;
@media screen and (max-width: 450px) {
 margin: 0px 10px 0px 0px;
}
  }
}

.left-item-plaza-news-duble {
  .left-item-plaza-news-duble__text {
    @include Text-Style-17;
    text-transform: uppercase;
  }
}


// ------------------------------------
.link-mail {
  @include flex();
  padding: 69px 0px 0px 0px;
  @media screen and (max-width: 600px) {
    padding: 0px 0px 0px 0px;
  }
  &__body {
    border: 2px solid #d4b584;
    flex: 0 1 99%;
    text-align: center;
  }

  &__form {
    @include jucb();
    @include alic();
    &_input {
      flex: 0 1 80%;
      &:hover > span {
      }
      input {
        border: 2px solid transparent;
        font-size: 18px;
        text-indent: 26px;
        width: 100%;
        height: 100%;
        padding: 10px 173px 10px 10px;
        @include anim(0.2s);
        font-family: PTSans;
        font-size: 15px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.67;
        letter-spacing: normal;
        text-align: left;
        color: $greyish-brown;
        @media screen and (max-width: 780px) {
          padding: 15px 0px 15px 10px;
        }
        &:hover {
          background-color: #d4b584;
        }
      }
    }

    &_submit {
      @include center();
      @include anim(0.1s);
      padding: 0px 0px 0px 20px;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        width: 32px;
        height: 1px;
        left: -5px;
        z-index: 2;
        top: 50%;
        @include rotate(90deg);
        background-color: #b29c69;
      }
      img {
        width: 100%;
        height: 100%;
        padding: 0px 16px 0px 0px;
      }
      input {
        font-size: 20px;
        width: 100%;
        height: 100%;
        padding: 10px 5px 10px 10px;
        text-transform: uppercase;
        border: 2px solid transparent;
        @include anim(0.2s);
        font-family: PFDinTextCondPro;
        font-size: 21.2px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.19;
        letter-spacing: 2.12px;
        text-align: left;
        color: $dark;
        background-color: transparent;
        @media screen and (max-width: 780px) {
          padding: 15px 10px 15px 10px;
        }
        &:hover {
          background-color: #d4b584;
        }
      }
    }
  }
}
