@import "common/mixins";

.header {
opacity: 0.95;
  background-color: $dark;
  position: relative; 
  position: fixed;
  z-index: 50;  
  width: 100%;
  &__container {
    @include alic();
    height: 75px;
    @include jucb();
    @media (max-width: 980px) {
      padding: 0px 10px;
    }
  }

  &__logo {
    flex: 0 1 20%;
    position: relative;
    z-index: 2;
    @include jucs();
    @media screen and (max-width: 768px) {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 144px;
    }
  }
  &__menu {
    flex: 0 1 60%;
  }

  &__actions {
    flex: 0 1 21%;
    @media screen and (max-width: 768px) {
      flex: 0 1 auto;
    }
  }
}

// ------------menu------------------------------
.menu {
  @include flex();
  @media screen and (max-width: 768px) {
    &__body {
      background-color: rgba(84, 89, 102, 0.815);
      position: fixed;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      padding: 80px 0px 20px 0px;
      overflow: auto;
      opacity: 0;
      visibility: hidden;
      @include anim(0.2s);

      &:after {
        content: "";
        width: 100%;
        position: fixed;
        height: 75px;
        top: 0;
        background: #1c242a;
      }

      &.active {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__list {
    
    @include flex();
    @media screen and (max-width: 768px) {
      display: block;
    }

    li {
      margin: 0px 15px 0px 0px;
      &:first-child {
        margin: 0px 15px;
      }
  
      @media screen and (max-width: 768px) {
        margin: 10px 15px;
      }
    }
  }

  &__link {
      @include Text-Style;
    text-transform: uppercase;

    @media screen and (max-width: 768px) {
      font-size: 20px;
     
    }
  }
}

// ------------------//  ----------------------------
.actions-header {
  @include alic();
  position: relative;
  z-index: 2;

  &__region {
    @extend .menu__link;
    line-height: 20px;
    @media screen and (max-width: 790px) {
      font-size: 12px;
    }

    &:before {
      content: "";
      width: 20px;
      height: 20px;
      margin: 0px 10px 0px 0px;
      background: url(../img/icons/icon-1.gif) center no-repeat;
    }
  }
}
.small {
  display: none;
}
@media screen and (max-width: 768px) {
  .small {
    display: flex;
  }
  .large {
    display: none;
  }
}
// ----- ----
.user {
  &-header {
    position: relative;
    &__icon {
      cursor: pointer;
      margin: 0px 0px 0px 7px;
    }
    &__menu {
      position: absolute;
      top: 48px;
      left: -160px;
      background-color: $dark;
      width: 180px;
      opacity: 0;
      visibility: hidden;
      @include anim(0.3s);
    }
    @media screen and (max-width: 768px) {
      &__menu {
        width: 100%;
        position: fixed;
        top: 78px;
        left: 0;
      }
    }
  }
}
._active {
  visibility: visible;
  opacity: 1;
}
.user-header li {
  background: url(../img/icons/icon-2.gif) 15px 5px no-repeat;
  padding: 2px 0px 2px 33px;

  &:last-child {
    padding: 2px 0px 9px 33px;
  }
}

.user-header li a {
  @include Text-Style;
}

// -----------------------------
.icon-menu {
  @media (max-width: 768px) {
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5;

    span {
      top: 8;
      left: 0px;
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: $sandy-brown;
      @include anim(0.3s);

      &:first-child {
        top: 8px;
      }

      &:last-child {
        top: auto;
        bottom: 0px;
      }
    }

    &.active {
      span {
        @include scale(0);
        background-color: #fff;

        &:first-child {
          @include rotate(-45deg);
          top: 8px;
        }

        &:last-child {
          @include rotate(45deg);
          bottom: 8px;
        }
      }
    }
  }
}
